@media only screen and (max-width: 375px) {

    .btn-link,
    .btn-links {
        display: flex;
        box-sizing: border-box;
        justify-content: center;
        align-items: center;
        flex: none;
        text-align: center;
    }

    .btn-links {
        flex-direction: column;
        margin: auto;
        padding: 0px;
        gap: 24px;
        align-self: stretch;
        order: 1;
        flex-grow: 0;
    }

    .btn-link {
        flex-direction: row;
        padding: 24px 32px;
        gap: 8px;
        text-decoration: none;
        width: 90%;
        height: 75px;
        padding-top: 30px;
        background: #EAECF0;
        color: #101828;
        border: 1px solid #EAECF0;
        border-radius: 8px;
        order: 0;
        flex-grow: 1;
        font-size: small;
        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
    }

    small {
        font-size: x-small;
    }

}

@media only screen and (min-width: 375px) {


.btn-link,
    .btn-links {
        display: flex;
        box-sizing: border-box;
        justify-content: center;
        align-items: center;
        flex: none;
        text-align: center;
    }

    .btn-links {
        flex-direction: column;
        margin: auto;
        padding: 0px;
        gap: 24px;
        align-self: stretch;
        order: 1;
        flex-grow: 0;
    }

.btn-link {
flex-direction: row;
padding: 24px 32px;
gap: 8px;
width: 90%;
text-decoration: none;
height: 76px;
background: #EAECF0;
border: 1px solid #EAECF0;
border-radius: 8px;
font-family: 'Inter', sans-serif;
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 28px;
color: #101828;
flex: none;
order: 0;
flex-grow: 0;
}

.btn-link:hover {
    background: #D0D5DD;
border: 1px solid #D0D5DD;
box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
border-radius: 8px;
}
small {
    font-size: small;
    color: #505154;
}

}